@import "../main";
@import "../vendor/tip-tip";
@import "../vendor/lity";
@import "parts/pro-badge";
@import "parts/vars";

.shipping-nova-poshta-for-woocommerce {
	&-logo {
		position: absolute;
		top: 0;
		right: 0;
		padding: 25px 30px;
		display: flex;
		align-items: flex-end;

		a {
			display: block;
			outline: none;
			box-shadow: none;
			width: 150px;
		}

		img {
			display: block;
			height: auto;
		}

		span {
			font-size: 20px;
			color: #666;
			margin-right: 10px;
			font-weight: 600;
			line-height: 1;
		}
	}

	&-row {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}

	&-column {
		max-width: 530px;
		padding: 0 15px;
	}

	&-form,
	&-form-field {
		.with-help-tip {
			display: flex;
			align-items: center;

			input[type=radio],
			input[type=checkbox] {
				position: relative;
				top: 2px;
			}
		}

		.help-tip {
			color: #666;
			display: block;
			font-family: 'dashicons', serif;
			font-size: 1.1em;
			font-style: normal;
			position: relative;
			vertical-align: middle;
			padding: 5px;
			cursor: help;
			line-height: 1;
			margin: 0 -5px 0 5px;

			&:after {
				speak: none;
				font-weight: 400;
				text-transform: none;
				-webkit-font-smoothing: antialiased;
				text-indent: 0;
				width: 16px;
				line-height: 16px;
				height: 16px;
				text-align: center;
				content: "ïˆ£";
				cursor: help;
				font-variant: normal;
				margin: 0;
				display: block;
			}
		}
	}

	&-wrap {
		border-top: 4px solid $primary;
		margin-top: 0;
		margin-left: -20px;
		margin-right: 0;
		padding: 15px 30px 0;

		a {
			color: $link;
			transition: .3s ease;

			&:hover {
				text-decoration: none;
			}
		}

		h2 {
			font-size: 18px;
			font-weight: 600;
		}

		h3 {
			font-size: 16px;
			font-weight: 500;
		}

		label {
			font-size: 16px;
			font-weight: 500;
		}

		input[type=tel],
		input[type=text],
		input[type=password],
		input[type=number],
		select,
		.select2-container {
			display: block;
			width: 100%;
			max-width: 100%;
			margin: 0;
			font-size: 16px;
		}

		input[type=tel],
		input[type=text],
		input[type=password],
		input[type=number],
		select,
		.select2-container .select2-selection__rendered,
		.select2-container .select2-search__field {
			height: 40px;
			line-height: 40px;
			font-weight: 400;
			padding: 0 12px;
		}

		.select2-container {
			.select2-selection--single,
			.select2-selection--multiple {
				height: 40px;
			}

			.select2-search__field {
				padding: 0 12px;
				min-height: 40px;
				font-size: 16px;
			}

			.select2-selection__rendered {
				line-height: 38px;
			}

			.select2-selection__arrow {
				height: 40px;
				right: 7px;
			}

			.select2-results {
				font-size: 16px;
			}

			.select2-results__option {
				padding: 11px 12px;
			}
		}

		.submit {
			margin-top: 1.125rem;
			padding-top: 0;
		}

		.button {
			height: 40px;
			font-size: 14px;
			padding: 0 18px;
			line-height: 40px;
			font-weight: 600;

			&-primary {
				background-color: $primary;

				&:hover {
					background-color: $primary_hover;
				}
			}
		}

		.shipping-nova-poshta-for-woocommerce-form {
			max-width: 500px;
			border-bottom: 1px solid $border;
			padding-bottom: 30px;
			margin-bottom: 30px;

			&:last-child {
				border-bottom: 0;
				padding-bottom: 0;
				margin-bottom: 0;
			}

			&--license {
				display: flex;
				flex-wrap: wrap;

				div {
					width: 100%;
					margin: 0;
					display: flex;

					.button {
						margin-left: .5em;
					}
				}
			}
		}

		.shipping-nova-poshta-for-woocommerce-tab {
			font-size: 16px;
			font-weight: 500;
			padding: 21px 20px;
			color: $text;
			text-decoration: none;
			position: relative;
			box-shadow: none !important;
			outline: none !important;
			transition: .3s ease;

			&:hover {
				background-color: rgba(20, 27, 56, .12);
			}

			&--active {
				color: $primary;

				&:after {
					content: '';
					position: absolute;
					display: block;
					height: 2px;
					width: 100%;
					top: 100%;
					left: 0;
					right: 0;
					background-color: $primary;
				}
			}
		}
	}

	&-nav-tab-wrapper {
		display: flex;
		border-bottom: 2px solid $border;
		padding-top: 2px;
		margin-bottom: 30px;
	}

	&-alert {
		display: inline-block;
		position: relative;
		padding: 1rem 1.5rem;
		border: 1px solid transparent;
		border-radius: .25rem;
		font-size: 16px;
		font-weight: 500;

		&--error {
			color: #721c24;
			background-color: #f8d7da;
			border-color: #f5c6cb;
		}
	}
}

#order_shipping_line_items {
	.select2-container {
		width: 100% !important;

		.select2-selection--single {
			height: 30px;

			.select2-selection__rendered {
				line-height: 30px;
			}

			.select2-selection__arrow {
				height: 28px;
			}
		}

		.select2-selection {
			font-size: 14px;
			line-height: 2;
			color: #32373c;
			border-color: #7e8993;
			box-shadow: none;
			border-radius: 3px;
			padding: 0 24px 0 8px;
			min-height: 30px;
			-webkit-appearance: none;
			background: #fff;
		}
	}
}

#woocommerce-order-items #order_shipping_line_items.shipping-nova-poshta-for-woocommerce .thumb div {
	margin: 0;
	width: 100%;

	&:before {
		content: '';
		display: block;
		width: 42px;
		height: 42px;
		background-image: url('../../img/nova-poshta.svg');
		background-size: cover;
	}
}
