@import "vendor/select2.min";

#shipping_nova_poshta_for_woocommerce_nonce_field {
	display: none !important;
}

#shipping_nova_poshta_for_woocommerce_city,
#shipping_nova_poshta_for_woocommerce_warehouse {
	transition: .3s ease;
	display: block;
	max-width: 100%;
	width: 100% !important;
	box-sizing: border-box;

	&.select2-hidden-accessible {
		display: none;
	}

	&.inactive,
	&.inactive + .select2 {
		cursor: no-drop;
		opacity: .75;
	}
}
